/** @jsx jsx */
import { css } from 'jimu-core';
export function DatePickerStyles(props) {
    const theme = props.theme;
    const dateListWidth = 100;
    return css `

    .jimu-date-picker-input-group{
      display: flex;
      align-items: center;
      width: 100%;

      .jimu-date-picker-input{
        flex-grow: 1;
        font-size: 14px;
        width: calc(100% - 32px);
        &:focus {
          border: 1px solid ${theme.sys.color.primary.main} !important;
        }
        .jimu-interactive-node:focus-within {
          outline-offset: -2px;
        }
      }

      .date-picker-select-con {
        z-index: 1;
        margin-left: -1px;
      }
    }

    .jimu-datepicker{
      min-width: 249px;
      padding: 0;
      color: ${theme.ref.palette.black};
      /*background-color: ${theme.ref.palette.neutral[500]};*/
      border-radius: 5px;

      .quick-nav-virtual-date {
        margin: 0.5rem 0.25rem;
        .virtual-date-container button{
          width: 20%;
          /* for rtl transform: rotateY(180deg); */
        }
      }


      .react-datepicker {
        width: 100%;
        color: ${theme.ref.palette.black};
        background-color: transparent;
        border: none;

        .react-datepicker__navigation--previous, .react-datepicker__navigation--next {
          .react-datepicker__navigation-icon {
            width: 9px; /** For mac safari: width is affected by inside hidden label. */
            &:before, &:after{
              border-color: ${theme.ref.palette.neutral[900]};
            }
          }
        }
        .react-datepicker__navigation--previous {
            right: unset;
            left: 2px;
            .react-datepicker__navigation-icon--previous{
              left: unset;
              right: -2px;
              &:before {
                transform: rotate(225deg);
                left: unset;
                right: -7px;
              }
            }
        }
        .react-datepicker__navigation--next {
          left: unset;
          right: 2px;
          .react-datepicker__navigation-icon--next{
            right: unset;
            left: -2px;
            &:before {
              transform: rotate(45deg);
              right: unset;
              left: -7px;
            }
          }
        }

        .react-datepicker__month-container{
          width: 100%;
          float: left;
          &:first-of-type{
            .react-datepicker__header {
              .react-datepicker__month-select, .react-datepicker__year-select {
                cursor: pointer;
                &:hover {
                  color: ${theme.sys.color.primary.main};
                }
              }
            }
            .react-datepicker__current-month.react-datepicker__current-month--hasYearDropdown.react-datepicker__current-month--hasMonthDropdown{
              display: none;
            }
            .react-datepicker__header__dropdown{
              font-size: 14px;
              .react-datepicker__year-dropdown, .react-datepicker__month-dropdown, .react-datepicker__month-year-dropdown{
                background-color: ${theme.ref.palette.white};
                .react-datepicker__year-option--selected,
                .react-datepicker__month-option--selected,
                .react-datepicker__month-year-option--selected {
                  right: unset;
                  left: 15px;
                }
              }

              .react-datepicker__year-read-view, .react-datepicker__month-read-view, .react-datepicker__month-year-read-view{
                border: none;
                visibility: visible !important;
                display: flex;
                align-items: center;
                flex-direction: row-reverse;
                &:hover {
                  color: ${theme.sys.color.primary.main};
                }

                .react-datepicker__month-read-view--down-arrow,
                .react-datepicker__year-read-view--down-arrow,
                .react-datepicker__month-year-read-view--down-arrow {
                  display: none;
                  /* border-color: ${theme.ref.palette.black};
                  border-width: 2px 2px 0 0;
                  height: 7px;
                  width: 7px;
                  display: block;
                  position: unset;
                  margin-left: 4px; */
                }
              }

              .react-datepicker__year-dropdown-container--select,
              .react-datepicker__month-dropdown-container--select,
              .react-datepicker__month-year-dropdown-container--select,
              .react-datepicker__year-dropdown-container--scroll,
              .react-datepicker__month-dropdown-container--scroll,
              .react-datepicker__month-year-dropdown-container--scroll {
                margin: 0 0.25rem;
              }

              .react-datepicker__year-option, .react-datepicker__month-option, .react-datepicker__month-year-option{
                font-weight: normal;
                line-height: 23px;
                &:hover{
                  background-color: ${theme.sys.color.primary.main};
                }
              }
              .react-datepicker__year-option{
                .react-datepicker__navigation {
                  width: 10px;
                  height: 10px;
                  border: 0.45rem solid transparent;
                  &.react-datepicker__navigation--years-upcoming{
                    border-bottom-color: ${theme.ref.palette.black};
                  }
                  &.react-datepicker__navigation--years-previous{
                    border-top-color: ${theme.ref.palette.black};
                  }
                }
              }
            }
          }

          .react-datepicker__day-name, .react-datepicker__day, .react-datepicker__time-name{
            margin: 0.2rem;
            width: calc((100% - 3.2rem)/7);
          }

          /* header */
          .react-datepicker__header {
            background-color: transparent;
            border: none;
            padding-top: 0;

            .react-datepicker__month-select, .react-datepicker__year-select {
              text-align: center;
              font-weight: bold;
              color: ${theme.ref.palette.black};
              background-color: transparent;
              border: none;
              -moz-appearance: none; /* hide arrow icon */
              -webkit-appearance: none;
              -o-appearance: none;
              appearance: none;

              option {
                background-color: ${theme.ref.palette.white};
                color: ${theme.ref.palette.black};
              }
            }
            .react-datepicker__year-select {
              width: 65px;
            }

            .react-datepicker__current-month, .react-datepicker-time__header, .react-datepicker-year-header{
              color: ${theme.ref.palette.black};
              font-size: 14px;
            }

            .react-datepicker__day-name, .react-datepicker__day, .react-datepicker__time-name{
              color: ${theme.ref.palette.neutral[800]};
            }
          }

          /* body */
          .react-datepicker__month{
            border-top: 1px solid ${theme.ref.palette.neutral[500]};

            .react-datepicker__day:hover, .react-datepicker__month-text:hover, .react-datepicker__quarter-text:hover {
              border-radius: 0.3rem;
              background-color: ${theme.sys.color.primary.light};
            }
            .react-datepicker__day-name, .react-datepicker__day, .react-datepicker__time-name {
              color: ${theme.ref.palette.black};
            }

            .react-datepicker__day.react-datepicker__day--outside-month{
              color: ${theme.ref.palette.neutral[800]};
            }
            .react-datepicker__day.react-datepicker__day--today{
              outline: 1px dashed ${theme.sys.color.primary.main};
              outline-offset: -1px;
              border-radius: 0.3rem;
              &:focus {
                outline-offset: 2px;
                outline: 2px solid ${theme.sys.color.action.focus};
              }
            }

            /*selected*/
            .react-datepicker__day--keyboard-selected,
            .react-datepicker__month-text--keyboard-selected,
            .react-datepicker__quarter-text--keyboard-selected,

            .react-datepicker__day--selected, .react-datepicker__day--in-selecting-range,
            .react-datepicker__day--in-range, .react-datepicker__month-text--selected,
            .react-datepicker__month-text--in-selecting-range, .react-datepicker__month-text--in-range,
            .react-datepicker__quarter-text--selected, .react-datepicker__quarter-text--in-selecting-range,
            .react-datepicker__quarter-text--in-range {
              background-color: ${theme.sys.color.primary.main};
            }
          }
        }

      }

      /*left date list*/
      .react-datepicker-date-list{
        position: absolute;
        top: 5px;
        left: 0; /*-${dateListWidth}px;*/
        height: 100%;
        border-right: 1px solid ${theme.ref.palette.neutral[500]};
        .btn{
          font-size: 13px;
          display: block;
          padding: 5px 0;
          width: 100px;
          color: ${theme.ref.palette.black};
          &.date-selected{
            background-color: ${theme.sys.color.primary.main};
          }
        }
      }

      /* footer */
      .jimu-datepicker-footer{
        width: calc(100% - 20px);
        margin: -5px 10px 0 10px;
        .month-bottom-line{
          width: 100%;
          display: flex;
          label{
            width: 0;
          }
          div{
            width: 50%;
            margin: 0;
            border-top: 1px solid ${theme.ref.palette.neutral[500]};
          }
        }

        .input-btn-container{
          display: flex;
          justify-content: flex-end;
          padding-bottom: 8px;
          .jimu-timePicker{
            .time-picker-inputs{
              display: flex;
              .time-picker-input{
                width: 45px;
                text-align: center;
                border: none;
                color: ${theme.ref.palette.black};
                background-color: ${theme.ref.palette.neutral[300]};
                .jimu-numeric-input-input{
                  padding: 0 3px;
                }
                .jimu-numeric-input-handler-wrap{
                  width: 1rem;
                }
              }
              .time-picker-connector{
                padding: 0 3px;
              }
            }
          }

          .btn-primary{
            min-width: 60px;
            max-width: 105px;
          }
        }

        &.jimu-datepicker-footer__has_time .input-btn-container{
          justify-content: space-between;
        }
      }

      &.jimu-datepicker-rtl {
        .react-datepicker {
          .react-datepicker__navigation--previous .react-datepicker__navigation-icon--previous::before {
            transform: rotate(45deg);
          }
          .react-datepicker__navigation--next .react-datepicker__navigation-icon--next::before {
            transform: rotate(225deg);
          }
        }

        .react-datepicker__month-container{
          &:first-of-type{
            .react-datepicker__header__dropdown{
              .react-datepicker__month-read-view--down-arrow,
              .react-datepicker__year-read-view--down-arrow,
              .react-datepicker__month-year-read-view--down-arrow {
                transform: rotate(225deg);
              }
            }
          }
        }
        .input-btn-container .jimu-timePicker .time-picker-inputs {
          flex-direction: row-reverse;
        }
      }

      &.has-two-month{
        min-width: 498px;
        .react-datepicker{
          .react-datepicker__month-container{
            width: 50%;
          }
        }

        @media (max-width: 497px) {
          .react-datepicker{
            width: 250px;
            max-height: 265px;
            overflow-y: scroll;
            overflow-x: hidden;
          }
        }
        @media (min-width: 498px) {
          .jimu-datepicker-footer .month-bottom-line label{
            width: 13px;
          }
        }
      }

      &.has-done-button{
        .react-datepicker {
          border: none;
        }
      }
    }

  `;
}
